@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

@import "_variables.scss"; // Importing global variables
@import "_mainWindow.scss"; // Rest of the imports are self explanitory
@import "_waybar.scss";
@import "_iframeWindow.scss";
@import "_menu.scss";
@import "_botBar.scss";
@import "_popups.scss";
@import "_aboutBlankChooser.scss";
@import "_transitions.scss";

html, body {
    margin: 0;
    padding: 0;
    font-family: 'Open Sans';
    color: #fff;

    background-image: $bgCol;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: $bgCol;
    overflow: hidden;
    height: 100%;
    /*
    height: 121vh;
    transform: translateY(-6.95vh);
    */
}

#particles-js {
    position: fixed;
    bottom: 0;
    left: 0;
    width: calc(100vw * $isParticles);
    height: calc(100vh * $isParticles);
    z-index: 0;
}
