.openerText {
    position: absolute;
    font-family: "Open Sans";
    font-size: 1.4vw;
    color: #fff;
    top: 45%;
}

.openerSubText {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    font-family: "Open Sans";
    color: #7c7777;
    font-size: 0.9vw;
    transform: translateY(810%);
}

.openerContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 45%;
    height: 50%;
    background-color: #f1f1f114;
    padding: 0.5em;
    border-radius: $borderRad;
}

.openerButton {
    transform: translateY(2vh);
    display: inline-block;
    background-color: transparent;
    border: $borderThickness solid $hoverBorderCol;
    color: #fff;
    font-size: 1.5vw;
    padding: 0.5em 1em;
    transition: background-color 0.3s ease;
    border-radius: $borderRad;
    text-align: center;
    box-shadow: $hoverShadowRad $hoverShadowCol;
    &:hover {
        background-color: color-mix(in srgb, $hoverBorderCol 20%, transparent);
        text-shadow: 0px 0px 6px $hoverBorderCol;
        cursor: pointer;
    }
}
